body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  line-height: 1.6;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
  margin: 0;
  padding: 0; }

html {
  margin-left: calc(100vw - 100%); }

h1, p, h2 {
  margin: 0;
  padding: 0; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

a:active,
a:visited,
a:focus {
  text-decoration: none; }

.wrapper {
  max-width: 1170px;
  margin: 0 auto;
  padding: 15px; }

.header {
  text-align: center;
  margin-bottom: 30px; }

.meet-block {
  margin-bottom: 15px; }
  .meet-block ul li {
    margin-left: 20px; }
  .meet-block .meet-block--title,
  .meet-block .meet-block--description {
    margin-bottom: 15px; }

.meet-block__button {
  margin-bottom: 40px; }
  .meet-block__button a {
    font-family: "Roboto", sans-serif;
    color: #000;
    text-decoration: none;
    border-bottom: 1px dashed #000;
    transition: all .2s; }
    .meet-block__button a:hover {
      color: #4d4d4d;
      border-color: #4d4d4d; }

.forms-item {
  margin-bottom: 20px;
  position: relative; }

.forms-item__label {
  display: block;
  font-weight: 700; }

.forms-item__input {
  height: 20px;
  padding-left: 10px;
  font-family: "Roboto", sans-serif; }

.forms-button {
  margin-bottom: 20px; }
  .forms-button p {
    font-weight: 700; }

.goal-items {
  display: flex;
  flex-wrap: wrap; }

.goal-item {
  text-align: center;
  position: relative;
  user-select: none;
  width: 60px;
  height: 60px;
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  margin-right: 15px;
  margin-bottom: 15px; }
  .goal-item:last-cgild {
    margin-right: 0; }
  .goal-item.active {
    background-color: #64e764; }
  .goal-item label {
    cursor: pointer;
    font-size: 16px;
    line-height: 16px;
    color: #fff;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    padding-top: 12px; }
    .goal-item label span {
      display: block;
      font-size: 12px;
      line-height: 16px;
      color: #fff; }
    .goal-item label input {
      width: 0;
      height: 0;
      position: absolute;
      opacity: 0; }

.go-go,
.cancel {
  margin-top: 15px;
  display: none; }
  .go-go.active,
  .cancel.active {
    display: block; }

.goal-block .goal-block--h3 {
  margin-top: 0;
  display: none; }
  .goal-block .goal-block--h3.active {
    display: block; }

.js-yourname {
  font-weight: 700; }

.information-block {
  margin-top: 30px; }
  .information-block p {
    font-style: italic;
    text-decoration: underline;
    color: rgba(0, 0, 0, 0.5);
    font-size: 12px; }

.toggle-mnu {
  display: none;
  width: 28px;
  height: 28px;
  margin-top: 5px;
  position: absolute;
  top: 0;
  left: 30px;
  z-index: 100; }
  @media only screen and (max-width: 768px) {
    .toggle-mnu {
      display: block; } }
  .toggle-mnu span {
    position: relative;
    display: block;
    width: 100%;
    height: 2px;
    background-color: #000;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
    .toggle-mnu span:after, .toggle-mnu span:before {
      content: "";
      position: absolute;
      left: 0;
      top: 9px; }
    .toggle-mnu span:after {
      top: 18px;
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
    .toggle-mnu span:before {
      width: 100%;
      height: 2px;
      background-color: #000;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
  .toggle-mnu.on span {
    background-color: transparent; }
    .toggle-mnu.on span:before {
      transform: rotate(45deg) translate(-1px, 0px); }
    .toggle-mnu.on span:after {
      transform: rotate(-45deg) translate(6px, -7px); }
